import React from 'react';

class HobbyOdd extends React.Component {
  render() {
    return <div class="row justify-content-center no-gutters">
                <div class="col-lg-6">
                    <img class="img-fluid mb-3 mb-lg-0" src={this.props.item.image} alt=""/>
                </div>
                <div class="col-lg-6 order-lg-first">
                    <div class="bg-black text-center h-100 project">
                        <div class="d-flex h-100">
                            <div class="project-text w-100 my-auto text-center text-lg-right">
                                <h4 class="text-white">{this.props.item.name}</h4>
                                <p class="mb-0 text-white-50">
                                     {this.props.item.description}
                                </p>
                                <hr class="d-none d-lg-block mb-0 mr-0"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>;
  }
}

export default HobbyOdd;
