import React, {Component} from 'react';
import ProjectCardEven from './projectCardEven';
import ProjectCardOdd from './projectCardOdd';

export default class ProjectList extends Component {
    render() {
        let projectComponents = [];

        for(let project of this.props.items) {
            if (project.code%2 == 0)
                projectComponents.push(<ProjectCardEven item = {project}/>);
            else
                projectComponents.push(<ProjectCardOdd item = {project}/>);
        }
        return <section id="projects" class="projects-section bg-light">
            <div class="container">
                <div class="row align-items-center no-gutters mb-4 mb-lg-5">
                    <div class="col-xl-8 col-lg-7">
                        <img class="img-fluid mb-3 mb-lg-0" src="images/logo.svg" alt=""/>
                    </div>
                    <div class="col-xl-4 col-lg-5">
                        <div class="featured-text text-center text-lg-left">
                            <h4>My website</h4>
                            <p class="text-black-50 mb-0">
                                This site uses React and NodeJS to run in the background, a new framework I recently picked up. Each card containing a project or hobby is being read from a json and then added to a component class used by React.
                            </p>
                        </div>
                    </div>
                </div>
                {projectComponents}
            </div>
        </section>
    }
}

