import React, {Component} from 'react';
import Hobby from './Hobby';
import HobbyOdd from './HobbyOdd';

export default class HobbyList extends Component {
    render() {
        let cardComponents = [];

        for(let hobby of this.props.items) {
            if (hobby.code%2 == 0)
                cardComponents.push(<Hobby item = {hobby}/>);
            else
                cardComponents.push(<HobbyOdd item = {hobby}/>);
        }
        return <section id="hobbies" class="projects-section bg-light">
            <div class="container">
                <div class="row align-items-center no-gutters mb-4 mb-lg-5">
                    <div class="col-xl-8 col-lg-7">
                        <img class="img-fluid mb-3 mb-lg-0" src="images/roadtrip.jpg" alt=""/>
                    </div>
                    <div class="col-xl-4 col-lg-5">
                    <div class="featured-text text-center text-lg-left">
                        <h4>Road Trips</h4>
                        <p class="text-black-50 mb-0">
                            I ever since I learned to drive it has always been a personal goal of mine to drive across Canada. Starting in Ontario, I have driven to Vancouver Island and back.
                            In the near future I plan on going out east so I can say I offically have been across Canada. 
                        </p>
                    </div>
                    </div>
                </div>
                {cardComponents}
            </div>
        </section>
    }
}

